import React from 'react';
import ButtonPercent from '../../common/ButtonPercent/ButtonPercent';

export default function({ setIsMonthly, monthly }) {
    return (
      <div className='flex_block'>
        <p>–í—ã–ø–ª–∞—Ç–∞ –ø—Ä–æ—Ü–µ–Ω—Ç–æ–≤</p>
        <div>
          <ButtonPercent
            active={monthly ? true : false} text='–µ–∂–µ–º–µ—Å—è—á–Ω–æ'
            onClick={() => { setIsMonthly(true); }}
            className='button'
          />
          <ButtonPercent
            active={monthly ? false : true} text='–≤ –∫–æ–Ω—Ü–µ —Å—Ä–æ–∫–∞'
            onClick={() => { setIsMonthly(false); }}
            className='button'
          />
        </div>
      </div>
    );
}


